.header{
	padding: 15px 0;

	&__info{
		font-size: 17px;
		color: #173d73;
		max-width: 245px;
	}
}
.top-block{
	width: 100%;
}
.social{
	&__item{
		width: 31px;
		height: 31px;
		margin: 5px;
	}
}
.facebook-link{
	background: url(../images/facebook.png)no-repeat;
}
.inst-link{
	background: url(../images/inst.png)no-repeat;
}
.promo{
	font-size: 12px;
	padding: 15px;
	border-radius: 5px;
	box-shadow: 0px 9px 24px 0px rgba(0, 0, 0, 0.14);
	max-width: 225px;

	&__link{
		font-size: 11px;
	}
	&__content{
		margin: 10px 0;
	}
	&__code{
		font-size: 31px;
	}
	&__date{
		padding-left: 10px;
	}
}
.phone{
	&__item{
		margin-bottom: 10px;
	}
	&__icon{
		width: 30px;
	}
	&__title{
		color: $darkblue;
		font-size: 13px;
	}
	&__number{
		font-weight: bold;
		font-size: 21px;
		color: $red;
	}
}