.nav{
	background: #173d73;
	color: #fff;

	&-menu{
		font-size: 14px;
		font-weight: bold;
		text-transform: uppercase;

		li{
			float: left;
			position: relative;

			&:before{
				content: "";
				width: 2px;
				height: 100%;
				position: absolute;
				z-index: 2;
				left: 0;
				top: 0;
				background: url(../images/border.png)no-repeat;
			}
			&:last-child{
				&:after{
					content: "";
					width: 2px;
					height: 100%;
					position: absolute;
					z-index: 2;
					right: 0;
					top: 0;
					background: url(../images/border.png)no-repeat;
				}
			}
		}
		a{
			color: #fff;
			display: block;
			padding: 17px 15px;
		}
	}
}
.search{
	width: 230px;
	position: relative;

	&__field{
		display: block;
		width: 100%;
		height: 30px;
		background: none;
		border: 1px solid #92a5c1;
		outline: 0;
		padding-left: 15px;
		padding-right: 15px;
		color: #fff;
		font-family: $regular;
		font-size: 14px;
	}
	&__btn{
		position: absolute;
		z-index: 2;
		right: 10px;
		top: 50%;
		width: 12px;
		height: 12px;
		display: block;
		border: 0;
		outline: 0;
		cursor: pointer;
		transform: translate(0, -50%);
		background: url(../images/search.png)no-repeat;
	}
}